<!--
 * @Description: 认证表格
 * @Version: Do not edit
 * @Autor: gitxinghui
 * @Date: 2020-11-24 01:50:51
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2021-01-15 02:22:31
-->
<template>
    <view class="things_width_full identity_form">
        <cu-custom bgColor="bg-gradual-blue" :isBack="true"><block slot="backText"></block><block slot="content">普通会员认证</block></cu-custom>
        <view class="formBox">
            <form @submit="formSubmit">
                <view class="cu-form-group">
                    <view class="title">姓名</view>
                    <input class="inputText" placeholder="输入您的姓名" placeholder-class="placeHolderText" name="real_name"/>
                </view>
                <view class="cu-form-group" v-if="pageType == 2">
                    <view class="title">法人姓名</view>
                    <input class="inputText" placeholder="输入企业的法人姓名" placeholder-class="placeHolderText" name="corporation_name"/>
                </view>
                <view class="cu-form-group">
                    <view class="title">手机号码</view>
                    <input class="inputText" placeholder="输入您的手机号码" placeholder-class="placeHolderText" name="mobile"/>
                </view>
                <view class="cu-form-group">
                    <view class="title">身份证号</view>
                    <input class="inputText" placeholder="输入您的身份证号码" placeholder-class="placeHolderText" name="id_code"/>
                </view>
                <!-- <view class="cu-form-group">
                    <view class="title">折扣比例</view>
                    <input class="inputText" placeholder="请选择折扣比例" placeholder-class="placeHolderText" :disabled="true" name="radio_type_label" :value="wPickerDiscount.label" @tap="wPickerDiscount.visible=true"/>
                    <input class="hide" name="radio_type" :value="wPickerDiscount.value"/>
                </view> -->
                <!-- 上传上传合同 -->
                <view class="uploadBox" v-if="pageType == 3">
                     <input class="hide" name="contract_pic_id" :value="contract_pic_id"/>
                    <view class="cu-bar">
                        <view class="action">
                            上传上传合同
                        </view>
                    </view>
                    <view class="cu-form-group">
                        <view class="grid grid-square flex-sub things_width_full upload">
                            <view v-if="contract_pic_id_value" class="things_width_full imageBox" @tap="viewImage" :data-url="contract_pic_id_value">
                                <image class="things_width_full " :src="contract_pic_id_value" mode="aspectFit"></image>
                                <view class="cu-tag text-white" @tap.stop="delImg" data-id="4">
                                    <text class='cuIcon-close'></text>
                                </view>
                            </view>
                            <view v-else class="things_width_full flex justify-center align-center uploadTip" data-id="4" @tap="chooseImage">
                                <image class="contractIcon" src="./../../../../static/images/things/identity/form/upload.png" mode="widthFix" />
                                <view class="uploadText businessTipText">上传营业执照</view>
                            </view>
                        </view>
                    </view>
                </view>
                <!-- 上传营业执照 -->
                <view class="uploadBox" v-if="pageType == 2">
                    <input class="hide" name="business_license_pic_id" :value="business_license_pic_id"/>
                    <view class="cu-bar">
                        <view class="action">
                            上传营业执照
                        </view>
                    </view>
                    <view class="cu-form-group">
                        <view class="grid grid-square flex-sub things_width_full upload">
                            <view v-if="business_license_pic_id_value" class="things_width_full imageBox" @tap="viewImage" :data-url="business_license_pic_id_value">
                                <image class="things_width_full " :src="business_license_pic_id_value" mode="aspectFit"></image>
                                <view class="cu-tag text-white" @tap.stop="delImg" data-id="3">
                                    <text class='cuIcon-close'></text>
                                </view>
                            </view>
                            <view v-else class="things_width_full flex justify-center align-center uploadTip" data-id="3" @tap="chooseImage">
                                <image class="businessIcon" src="./../../../../static/images/things/identity/form/businessIcon.png" mode="widthFix" />
                                <view class="uploadText businessTipText">上传营业执照</view>
                            </view>
                        </view>
                    </view>
                </view>
                <!-- 身份证正面上传 -->
                <input class="hide" name="identity_positive_pic_id" :value="identity_positive_pic_id"/>
                <view class="uploadBox">
                    <view class="cu-bar">
                        <view class="action">
                            上传身份证正面
                        </view>
                    </view>
                    <view class="cu-form-group">
                        <view class="grid grid-square flex-sub things_width_full upload">
                            <view v-if="identity_positive_pic_id_value" class="things_width_full imageBox" @tap="viewImage" :data-url="identity_positive_pic_id_value">
                                <image class="things_width_full " :src="identity_positive_pic_id_value" mode="aspectFit"></image>
                                <view class="cu-tag text-white" @tap.stop="delImg" data-id="2">
                                    <text class='cuIcon-close'></text>
                                </view>
                            </view>
                            <view v-else class="things_width_full flex justify-center align-center uploadTip" data-id="2" @tap="chooseImage">
                                <image class="idCardImage" src="./../../../../static/images/things/identity/form/idCardZ.png" mode="widthFix" />
                                <view class="uploadText">上传身份证正面</view>
                            </view>
                        </view>
                    </view>
                </view>
                <!-- 身份证反面上传 -->
                <input class="hide" name="identity_back_pic_id" :value="identity_back_pic_id"/>
                <view class="uploadBox idCardF">
                    <view class="cu-bar">
                        <view class="action">
                            上传身份证反面
                        </view>
                    </view>
                    <view class="cu-form-group">
                        <view class="grid grid-square flex-sub things_width_full upload">
                            <view v-if="identity_back_pic_id_value" class="things_width_full imageBox" @tap="viewImage" :data-url="identity_back_pic_id_value">
                                <image class="things_width_full " :src="identity_back_pic_id_value" mode="aspectFit"></image>
                                <view class="cu-tag text-white" @tap.stop="delImg" data-id="1">
                                    <text class='cuIcon-close'></text>
                                </view>
                            </view>
                            <view v-else class="things_width_full flex justify-center align-center uploadTip" data-id="1" @tap="chooseImage">
                                <image class="idCardImage" src="./../../../../static/images/things/identity/form/idCardF.png" mode="widthFix" />
                                <view class="uploadText">上传身份证反面</view>
                            </view>
                        </view>
                    </view>
                </view>
                <!-- 认证费用 -->
                <view class="flex justify-start align-end cerCost">
                    <view class="costType">认证费用：</view>
                    <view class="costNum">￥{{memberMeal[pageType-1].price}}</view>
                    <input class="hide" name="cost" :value="memberMeal[pageType-1].price"/>
                </view>
                <!-- 提交按钮 -->
                <view class="flex justify-center align-end" :class="pageType == 1 ? 'submitBtn0':(pageType == 2 ? 'submitBtn1':'submitBtn2')">
                    <button class="cu-btn text-white things_bg_active_color submitBtn" form-type="submit" :disabled="buttonType">提交并支付</button>
                </view>
                <!-- 折扣类型 -->
                <w-picker 
                    :visible.sync="wPickerDiscount.visible"
                    mode="selector"
                    :options="base_discountData"
                    @confirm="selectDiscount($event,'selector')"
                    themeColor="#32d08b"
                    ref="discountPicker">
                </w-picker>
            </form>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
    import imageFun from './../../../../utils/images';
    import payFun from './../../../../utils/pay';
    import { generalMemberCertification_api,enterpriseMemberCertification_api,spokesmanMemberCertification_api,upload_image_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
	let that = null;
export default {
    data() {
        return {
            pageType: 1,// 身份类型，1：普通会员，2：企业会员，3：代言人
            index: 0,
            uploadImage: [],
            // 弹框
            popLoadModal: false,
            popDiaModal: false,
            popTipModal: {
                type: false,
                tipTitle: '',
                tipContent: ''
            },
            //套餐内容
            memberMeal: [
                {
                    id: 1,
                    name: '普通会员',
                    price: 0
                },
                {
                    id: 2,
                    name: '企业会员',
                    price: 0.01
                },
                {
                    id: 3,
                    name: '代言人',
                    price: 0.01
                }
            ],
            // 折扣类型
            wPickerDiscount: {
                visible: false,
                value: '',
                label: '',
                list: [],
            },
            contract_pic_id: '',//合同照片id
            contract_pic_id_value: '',
            business_license_pic_id:'',//营业执照照片id
            business_license_pic_id_value: '',
            identity_positive_pic_id: '',//身份证正面照片id
            identity_positive_pic_id_value: '',
            identity_back_pic_id: '',//身份证反面照片id
            identity_back_pic_id_value: '',
        }
    },
    computed: {
        ...mapGetters(['base_discountData']) 
    },
    methods: {
        ...mapMutations([
            'modal',
            'user',
            'base'
        ]),
        // 图片上传
        chooseImage(e) {
            imageFun.chooseImage(1,this).then(data=>{
                let params = data[0];
                upload_image_api(params,that).then(res=>{
                    console.log(res);
                    that.updateImage(e.currentTarget.dataset.id,res.data.file_id,data[0]);
                }).catch(err=>{
                    console.log(err);
                })
            }).catch(err=>{
                console.log(err);
            });
        },
        /**更新图片数据
         * @description: 
         * @param {*} num(1:身份证反面，2：身份证正面，3：营业执照，4：合同)
         * @param {*} data
         * @return {*}
         */
        updateImage(num = 1,file_id,file) {
            switch (Number(num)) {
                case 1:
                    that.identity_back_pic_id_value = file;
                    that.identity_back_pic_id = file_id;
                    break;
                case 2:
                    that.identity_positive_pic_id_value = file;
                    that.identity_positive_pic_id = file_id;
                    break;
                case 3:
                    that.business_license_pic_id_value = file;
                    that.business_license_pic_id = file_id;
                    break;
                case 4:
                    that.contract_pic_id_value = file;
                    that.contract_pic_id = file_id;
                    break;
                default:
                    break;
            }
        },
        viewImage(e) {
            imageFun.viewImage([e.currentTarget.dataset.url],e.currentTarget.dataset.url);
        },
        delImg(e) {
            uni.showModal({
                title: '提示',
                content: '确定删除图片吗',
                cancelText: '取消',
                confirmText: '确定',
                success: res => {
                    if (res.confirm) {
                        switch (Number(e.currentTarget.dataset.id)) {
                            case 1:
                                that.identity_back_pic_id_value = '';
                                that.identity_back_pic_id = '';
                                break;
                            case 2:
                                that.identity_positive_pic_id_value = '';
                                that.identity_positive_pic_id = '';
                                break;
                            case 3:
                                that.business_license_pic_id_value = '';
                                that.business_license_pic_id = '';
                                break;
                            case 4:
                                that.contract_pic_id_value = '';
                                that.contract_pic_id = '';
                                break;
                            default:
                                break;
                        }
                    }
                }
            })
        },
        // 关闭弹框
        closeModal: function(e) {
            this.$wx.closeModal(this,e.type);
        },
        /**提交表单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        formSubmit(e) {
            that.buttonType = true;
            // 定义表单规则
            let rule = [
                {name:"real_name", checkType:"notnull", checkRule:"",  errorMsg:"请输入真实姓名"},
                {name:"mobile", checkType:"notnull", checkRule:"",  errorMsg:"请输入手机号码"},
                {name:"identity_positive_pic_id", checkType: "notnull", checkRule:"",  errorMsg:"请选择身份证正面照上传"},
                {name:"identity_back_pic_id", checkType: "notnull", checkRule:"",  errorMsg:"请选择身份证反面照上传"},
                {name:"id_code", checkType:"notnull", checkRule:"",  errorMsg:"请输入身份证号码"},
            ];
            switch (Number(that.pageType)) {
                case 2:
                    rule = [{name:"business_license_pic_id", checkType:"notnull", checkRule:"",  errorMsg:"请选择营业执照上传"},...rule];
                    break;
                case 3:
                    rule = [{name:"contract_pic_id", checkType:"notnull", checkRule:"",  errorMsg:"请选择合同照上传"},...rule];
                    break;
                default:
                    break;
            }
            //进行表单检查
            let formData = e.detail.value;
            let checkRes = checkForm.check(formData, rule);
            if(!checkRes){
                that.modal({name:'popTipModal',value:{type: true,tipTitle:'表单错误',tipContent:checkForm.error}});
                that.buttonType = false;
                return false;
            }

            that.modal({name:'popLoading',value:true});
            switch (Number(that.pageType)) {
                case 1:
                    generalMemberCertification_api(formData,that).then(res=>{
                        that.modal({name:'popLoading',value:false});
                        that.buttonType = false;
                        that.base({name:'refreshPage',value:true});
                        that.modal({name:'popLinkDialog',value: {type:true,tipTitle:'提示',tipContent:'提交成功，等待审核',linkUrl:2}});
                    }).catch(err=>{
                        console.log(err);
                        that.modal({name:'popLoading',value:false});
                        that.buttonType = false;
                    })
                    break;
                case 2:
                    enterpriseMemberCertification_api(formData,that).then(res=>{
                        // 支付订单
                        payFun.wxPay(res.payment,that).then(res=>{
                            that.modal({name:'popLoading',value:false});
                            that.buttonType = false;
                            that.base({name:'refreshPage',value:true});
                            that.modal({name:'popLinkDialog',value: {type:true,tipTitle:'提示',tipContent:'提交成功，等待审核',linkUrl:'/pages/home/index?pageCur=user'}});
                        }).catch(err=>{
                            console.log(err);
                            that.modal({name:'popLoading',value:false});
                            that.buttonType = false;
                        })
                    }).catch(err=>{
                        console.log(err);
                        that.modal({name:'popLoading',value:false});
                        that.buttonType = false;
                    })
                    break;
                case 3:
                    spokesmanMemberCertification_api(formData,that).then(res=>{
                        // 支付订单
                        payFun.wxPay(res.payment,that).then(res=>{
                            that.modal({name:'popLoading',value:false});
                            that.buttonType = false;
                            that.base({name:'refreshPage',value:true});
                            that.modal({name:'popLinkDialog',value: {type:true,tipTitle:'提示',tipContent:'提交成功，等待审核',linkUrl:'/pages/home/index?pageCur=user'}});
                        }).catch(err=>{
                            console.log(err);
                            that.modal({name:'popLoading',value:false});
                            that.buttonType = false;
                        })
                    }).catch(err=>{
                        console.log(err);
                        that.modal({name:'popLoading',value:false});
                        that.buttonType = false;
                    })
                    break;
                default:
                    break;
            }
        },
        // 页面生命周期
        onLoad: function (options) {
            that = this;
            that.pageType = options.pageType;
        }
    }
}
</script>
<style>
    page {
        width: 100vw;
        height: 100vh;
        background-color: #FFF;
    }
</style>
<style lang="scss" scoped>
    @import "./index"
</style>